import React, { useEffect, useState } from 'react';
import fetch from 'cross-fetch';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import InputAdornment from '@mui/material/InputAdornment';
import Link from '@mui/material/Link';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Tooltip from '@mui/material/Tooltip';
import Typography from '@mui/material/Typography';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import MessageIcon from '@mui/icons-material/Message';
import LaunchIcon from '@mui/icons-material/Launch';
import MenuItem from '@mui/material/MenuItem';
import SaveAsIcon from '@mui/icons-material/SaveAs';
import SendIcon from '@mui/icons-material/Send';
import UndoIcon from '@mui/icons-material/Undo';
import UpdateIcon from '@mui/icons-material/Update';
import VisibilityIcon from '@mui/icons-material/Visibility';
import dateFormat from '@libs/dateFormat';
import qr from 'qrcode';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const ClientsFriends = props => {
  const [initialFriendName, setInitialFriendName] = useState(
    props.clientsFriend.friendName
  );
  const [initialYourName, setInitialYourName] = useState(
    props.clientsFriend.yourName
  );
  const [initialNote, setInitialNote] = useState(props.clientsFriend.note);
  const [uuid, setUuid] = useState(props.clientsFriend.uuid);
  const [friendName, setFriendName] = useState(props.clientsFriend.friendName);
  const [yourName, setYourName] = useState(props.clientsFriend.yourName);
  const [note, setNote] = useState(props.clientsFriend.note);
  const [disabledUpdateButton, setDisabledUpdateButton] = useState(true);
  const [disabledUndoButton, setDisabledUndoButton] = useState(true);
  const [validTimestamp, setValidTimestamp] = useState(
    props.clientsFriend.validTimestamp
  );
  const [createdAt, setCreatedAt] = useState(props.clientsFriend.createdAt);
  const [updatedAt, setUpdatedAt] = useState(props.clientsFriend.updatedAt);
  const [createdBy, setCreatedBy] = useState(props.clientsFriend.createdBy);
  const [updatedBy, setUpdatedBy] = useState(props.clientsFriend.updatedBy);

  const [isOpenDialog, setIsOpenDialog] = useState(false);
  const [isOpenDialogUrl, setIsOpenDialogUrl] = useState(false);

  const [shareUrl, setShareUrl] = useState('');
  const [qrImage, setQrImage] = useState('');

  useEffect(() => {
    const _shareUrl = `${process.env.URL}foryou/?uuid=${props.clientsFriend.uuid}`;

    const f = async () => {
      try {
        const _qrImage = await qr.toDataURL(_shareUrl);
        setQrImage(_qrImage);
      } catch (err) {
        console.error(err);
      }
    };

    f();

    setShareUrl(_shareUrl);

    setInitialFriendName(props.clientsFriend.friendName);
    setInitialYourName(props.clientsFriend.yourName);
    setInitialNote(props.clientsFriend.note);
    setUuid(props.clientsFriend.uuid);
    setFriendName(props.clientsFriend.friendName);
    setYourName(props.clientsFriend.yourName);
    setNote(props.clientsFriend.note);
    setDisabledUpdateButton(true);
    setDisabledUndoButton(true);
    setValidTimestamp(props.clientsFriend.validTimestamp);
    setCreatedAt(props.clientsFriend.createdAt);
    setUpdatedAt(props.clientsFriend.updatedAt);
    setCreatedBy(props.clientsFriend.createdBy);
    setUpdatedBy(props.clientsFriend.updatedBy);
  }, [
    props.clientsFriend.uuid,
    props.clientsFriend.friendName,
    props.clientsFriend.yourName,
    props.clientsFriend.note,
    props.clientsFriend.validTimestamp,
    props.clientsFriend.createdAt,
    props.clientsFriend.updatedAt,
    props.clientsFriend.createdBy,
    props.clientsFriend.updatedBy
  ]);

  const handleChangeFriendName = ({ target: { value } }) => {
    const isDiff =
      value !== initialFriendName ||
      yourName !== initialYourName ||
      note !== initialNote;

    setDisabledUpdateButton(!(value && isDiff));
    setDisabledUndoButton(!isDiff);
    setFriendName(value);
  };

  const handleChangeYourName = ({ target: { value } }) => {
    const isDiff =
      friendName !== initialFriendName ||
      value !== initialYourName ||
      note !== initialNote;

    setDisabledUpdateButton(!(friendName && isDiff));
    setDisabledUndoButton(!isDiff);
    setYourName(value);
  };

  const handleChangeNote = ({ target: { value } }) => {
    const isDiff =
      friendName !== initialFriendName ||
      yourName !== initialYourName ||
      value !== initialNote;

    setDisabledUpdateButton(!(friendName && isDiff));
    setDisabledUndoButton(!isDiff);
    setNote(value);
  };

  const handleClickExtendButton = async () => {
    try {
      const res = await fetch(
        `${process.env.API_URL}/api/partners/clients/friends/${uuid}`,
        {
          method: 'PUT',
          body: JSON.stringify({
            validTimestamp: new Date()
          })
        }
      );

      if (res.status >= 400) {
        throw new Error('Bad response /api/partners/clients');
      }

      const { validTimestamp: _validTimestamp } = await res.json();

      console.log(_validTimestamp);

      setValidTimestamp(_validTimestamp);
    } catch (e) {
      alert('error');
      console.log(e);
    }
  };

  const handleClickUpdateButton = async () => {
    try {
      const res = await fetch(
        `${process.env.API_URL}/api/partners/clients/friends/${uuid}`,
        {
          method: 'PUT',
          body: JSON.stringify({
            friendName,
            yourName,
            note,
            updatedBy: 'user'
          })
        }
      );

      if (res.status >= 400) {
        throw new Error('Bad response /api/partners/clients');
      }

      const {
        friendName: _friendName,
        yourName: _yourName,
        note: _note,
        createdAt: _createdAt,
        updatedAt: _updatedAt,
        createdBy: _createdBy,
        updatedBy: _updatedBy
      } = await res.json();

      setInitialFriendName(_friendName);
      setInitialYourName(_yourName);
      setInitialNote(_note);

      setFriendName(_friendName);
      setYourName(_yourName);
      setNote(_note);
      setDisabledUpdateButton(true);
      setDisabledUndoButton(true);
      setCreatedAt(_createdAt);
      setUpdatedAt(_updatedAt);
      setCreatedBy(_createdBy);
      setUpdatedBy(_updatedBy);
    } catch (e) {
      alert('error');
      console.log(e);
    }
  };

  const handleClickUndoButton = () => {
    setFriendName(initialFriendName);
    setYourName(initialYourName);
    setNote(initialNote);
    setDisabledUpdateButton(true);
    setDisabledUndoButton(true);
  };

  const handleClickDeleteButton = () => setIsOpenDialog(true);

  const handleClickDeleteOk = async () => {
    try {
      const res = await fetch(
        `${process.env.API_URL}/api/partners/clients/friends/${uuid}`,
        {
          method: 'DELETE'
        }
      );

      if (res.status >= 400) {
        throw new Error('Bad response /api/partners/clients/friends');
      }

      props.reload();
      setIsOpenDialog(false);
    } catch (e) {
      console.log(e);
    }
  };

  const handleCloseDialog = () => setIsOpenDialog(false);
  const handleCloseDialogUrl = () => setIsOpenDialogUrl(false);

  const CopyShareUrl = () => {
    const [open, setOpen] = useState(false);
    const handleTooltipOpen = () => setOpen(true);
    const handleTooltipClose = () => setOpen(false);

    return (
      <CopyToClipboard text={shareUrl} onCopy={handleTooltipOpen}>
        <Tooltip
          onClose={handleTooltipClose}
          open={open}
          title='コピーしました'
        >
          <Button
            fullWidth
            variant='outlined'
            size='small'
            startIcon={<ContentCopyIcon />}
          >
            URLをコピーする
          </Button>
        </Tooltip>
      </CopyToClipboard>
    );
  };

  return (
    <>
      <TableRow
        hover={true}
        selected={!disabledUpdateButton || !disabledUndoButton}
      >
        <TableCell>
          <Box display='flex' flexDirection='column'>
            <Box>
              <Button
                fullWidth
                variant='outlined'
                size='small'
                startIcon={<VisibilityIcon />}
                onClick={() => {
                  setIsOpenDialogUrl(true);
                }}
              >
                QRを表示する
              </Button>
              <Box mt={1}>
                <CopyShareUrl />
              </Box>
              <Box mt={1}>
                <Button
                  fullWidth
                  variant='outlined'
                  size='small'
                  startIcon={<SendIcon />}
                  onClick={() => {
                    window.location.href = `mailto:?subject=一緒にコンパクトウシを始めませんか？&body=${friendName}様%0D%0A%0D%0A【コンパクトウシのご案内】%0D%0A${shareUrl}%0D%0A%0D%0A${yourName}`;
                  }}
                >
                  メールで送る
                </Button>
              </Box>
              <Box mt={1}>
                <Button
                  fullWidth
                  variant='outlined'
                  size='small'
                  color='success'
                  startIcon={<MessageIcon />}
                  onClick={() => {
                    const lineUrl = `https://social-plugins.line.me/lineit/share?url=${shareUrl}`;
                    window.open(lineUrl, '_blank', 'noreferrer');
                  }}
                >
                  LINEで送る
                </Button>
              </Box>
              <Box mt={1}>
                <Button
                  fullWidth
                  size='small'
                  color='warning'
                  endIcon={<LaunchIcon />}
                  onClick={() => window.open(shareUrl, '_blank', 'noreferrer')}
                >
                  ご紹介ページを確認する
                </Button>
              </Box>
            </Box>
            <Dialog open={isOpenDialogUrl} onClose={handleCloseDialogUrl}>
              <DialogTitle>
                <Typography variant='h6' textAlign='center'>
                  {friendName}様へのコンパクトウシご案内URL
                </Typography>
              </DialogTitle>
              <DialogContent>
                <DialogContentText>
                  <img
                    src={qrImage}
                    style={{
                      display: 'block',
                      margin: '0 auto',
                      width: '100px',
                      height: '100px'
                    }}
                  />
                  {shareUrl}
                  <CopyShareUrl />
                </DialogContentText>
              </DialogContent>
            </Dialog>
          </Box>
        </TableCell>
        <TableCell>
          <TextField
            value={friendName}
            onChange={handleChangeFriendName}
            InputProps={{
              endAdornment: <InputAdornment position='end'>様</InputAdornment>
            }}
          />
        </TableCell>
        <TableCell>
          <TextField
            value={yourName}
            onChange={handleChangeYourName}
            InputProps={{
              endAdornment: <InputAdornment position='end'>様</InputAdornment>
            }}
          />
        </TableCell>
        <TableCell>
          <Select
            value={note}
            onChange={handleChangeNote}
            inputProps={{ readOnly: true }}
          >
            <MenuItem value='職場同僚'>職場同僚</MenuItem>
            <MenuItem value='友人'>友人</MenuItem>
            <MenuItem value='親族'>親族</MenuItem>
            <MenuItem value='取引先'>取引先</MenuItem>
            <MenuItem value='その他'>その他</MenuItem>
          </Select>
        </TableCell>
        <TableCell>
          <Stack direction='column' spacing={1}>
            <Tooltip title='有効期限を延長する'>
              <span>
                <IconButton
                  color='primary'
                  aria-label='update'
                  size='large'
                  onClick={handleClickExtendButton}
                >
                  <UpdateIcon fontSize='inherit' />
                </IconButton>
              </span>
            </Tooltip>
            <Tooltip title='このレコードの変更を保存する'>
              <span>
                <IconButton
                  color='primary'
                  aria-label='update'
                  size='large'
                  disabled={disabledUpdateButton}
                  onClick={handleClickUpdateButton}
                >
                  <SaveAsIcon fontSize='inherit' />
                </IconButton>
              </span>
            </Tooltip>
            <Tooltip title='このレコードの変更を元に戻す'>
              <span>
                <IconButton
                  color='warning'
                  aria-label='undo'
                  size='large'
                  disabled={disabledUndoButton}
                  onClick={handleClickUndoButton}
                >
                  <UndoIcon fontSize='inherit' />
                </IconButton>
              </span>
            </Tooltip>
            <Tooltip title='このレコードを削除する'>
              <span>
                <IconButton
                  color='error'
                  aria-label='delete'
                  size='large'
                  onClick={handleClickDeleteButton}
                >
                  <DeleteForeverIcon fontSize='inherit' />
                </IconButton>
              </span>
            </Tooltip>
          </Stack>
        </TableCell>
        <TableCell>
          <Typography variant='body2' color='primary'>
            {dateFormat(
              (() => {
                // @TODO: refactoring
                const expireTimestamp = new Date(validTimestamp);
                expireTimestamp.setMonth(expireTimestamp.getMonth() + 1);
                return expireTimestamp;
              })()
            )}{' '}
            <small>(有効期限)</small>
          </Typography>
          <br />
          <Typography variant='body2'>
            {dateFormat(createdAt)} <small>作成</small>
          </Typography>
          <Typography variant='body2'>
            {dateFormat(updatedAt)} <small>更新</small>
          </Typography>
        </TableCell>
      </TableRow>
      <Dialog
        open={isOpenDialog}
        onClose={handleCloseDialog}
        aria-labelledby='alert-dialog-title'
        aria-describedby='alert-dialog-description'
      >
        <DialogTitle id='alert-dialog-title'>
          このレコードの削除しますか？
        </DialogTitle>
        <DialogContent>
          <DialogContentText id='alert-dialog-description'>
            ご友人、ご知人のお名前　: {initialFriendName}
            <br />
            お名前　: {initialYourName}
            <br />
            メモ　　: {initialNote}
            <br />
            作成日時: {dateFormat(createdAt)}
            <br />
            更新日時: {dateFormat(updatedAt)}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>キャンセル</Button>
          <Button onClick={handleClickDeleteOk} autoFocus>
            削除する
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default ClientsFriends;
