import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import fetch from 'cross-fetch';
import Alert from '@mui/material/Alert';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import FormControl from '@mui/material/FormControl';
import InputAdornment from '@mui/material/InputAdornment';
import InputLabel from '@mui/material/InputLabel';
import LinearProgress from '@mui/material/LinearProgress';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Snackbar from '@mui/material/Snackbar';
import Stack from '@mui/material/Stack';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import AddIcon from '@mui/icons-material/Add';
import ClientsFriendsTableRow from '@components/invitations/clients-friends';

const InvitationsIndexPage = () => {
  const [isOpenSnackbar, setIsOpenSnackbar] = useState(false);
  const [severity, setSeverity] = useState('info');
  const [message, setMessage] = useState('');

  // 新規作成フォーム
  const [newFriendName, setNewFriendName] = useState('');
  const [newYourName, setNewYourName] = useState('');
  const [newFriendNote, setNewFriendNote] = useState('');
  const [isDisabledNewButton, setIsDisabledNewButton] = useState(true);
  const [partnersClientUuid, setPartnersClientUuid] = useState('');
  const [clientsFriends, setClientsFriends] = useState();
  const [yourName, setYourName] = useState('');
  const [yourPartnerName, setYourPartnerName] = useState('');
  const [isNotFound, setIsNotFound] = useState(false);

  const [reload, setReload] = useState(0);

  const handleCloseSnackbar = (e, reason) => {
    if (reason === 'clickaway') return;
    setIsOpenSnackbar(false);
  };

  const handleChangeNewClientNote = ({ target: { value } }) => {
    setNewFriendNote(value);
    setIsDisabledNewButton(!newYourName || !newFriendName || !value);
  };

  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const uuid = params.get('uuid');
    setPartnersClientUuid(uuid);
  }, []);

  useEffect(() => {
    if (!partnersClientUuid) return;

    const f = async () => {
      try {
        const resInfo = await fetch(
          `${process.env.API_URL}/api/partners/clients/${partnersClientUuid}`
        );

        if (resInfo.status === 404) {
          setIsNotFound(true);
          throw new Error('Bad response /api/partners/clients/{uuid}');
        }

        const res = await fetch(
          `${process.env.API_URL}/api/partners/clients/friends?partners-client-uuid=${partnersClientUuid}`
        );

        if (res.status >= 400) {
          throw new Error('Bad response /api/partners/clients/friends{uuid}');
        }

        const infoData = await resInfo.json();
        const data = await res.json();

        setYourName(infoData.name);
        setYourPartnerName(infoData.partner.lastNameKanji);
        setClientsFriends(data);

        setNewYourName(infoData.name);
      } catch (e) {
        console.log(e);
      }
    };

    f();
  }, [reload, partnersClientUuid, isNotFound]);

  if (isNotFound) {
    return (
      <Typography align='center' mt={5}>
        お探しのページは削除されたか見つかりません。
        <br />
        <a href='https://daruma-inv.com/'>https://daruma-inv.com/</a>
      </Typography>
    );
  }

  return !partnersClientUuid ? (
    <></>
  ) : (
    <>
      <Helmet>
        <title>お客様ご紹介ページ | DARUMA, Inc.</title>
      </Helmet>
      <AppBar position='fixed' sx={{ backgroundColor: '#ef8468' }}>
        <Container maxWidth='xl'>
          <Toolbar disableGutters>
            <Typography
              variant='h6'
              noWrap
              fontWeight='bold'
              sx={{ mr: 2, display: 'flex', alignItems: 'center' }}
            >
              {yourName} 様専用 お客様ご紹介ページ <small>(by DARUMA)</small>
            </Typography>
            <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }} />
            <Box display='flex' flexDirection='row' alignItems='center'>
              <Box
                display='flex'
                flexDirection='column'
                alignItems='center'
                justifyContent='center'
              >
                <Typography fontSize={14} fontWeight='bold'>
                  DARUMA, Inc.
                </Typography>
                <Typography fontSize={10}>[お客様限定]</Typography>
              </Box>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <Container
        maxWidth='xl'
        sx={{
          mt: 10
        }}
      >
        <Typography>
          <strong>{yourName}</strong>様、DARUMAの {yourPartnerName}{' '}
          です。いつもお世話になっております。
        </Typography>
        <Typography>
          この機会にぜひご友人、ご知人に DARUMA をご紹介くださいませ。
        </Typography>
        <Typography variant='body2' color='error'>
          ※URLは有効期限1カ月となり、期日を過ぎますとアクセス出来なくなりますのでご了承下さい
        </Typography>
        <Box
          display='flex'
          my={2}
          pl={2}
          py={4}
          sx={{
            border: '2px solid #ccc',
            borderRadius: 2
          }}
        >
          <TextField
            label='ご友人・ご知人のお名前（ニックネーム可）'
            variant='outlined'
            value={newFriendName}
            sx={{
              width: 320
            }}
            InputProps={{
              endAdornment: <InputAdornment position='end'>様</InputAdornment>
            }}
            onChange={e => {
              const _newFriendName = e.target.value;
              setNewFriendName(_newFriendName);
              setIsDisabledNewButton(
                !newYourName || !_newFriendName || !newFriendNote
              );
            }}
          />

          <TextField
            label='お客様のご氏名（ニックネーム可）'
            variant='outlined'
            value={newYourName}
            InputProps={{
              endAdornment: <InputAdornment position='end'>様</InputAdornment>
            }}
            sx={{
              width: 240
            }}
            onChange={e => {
              const _newYourName = e.target.value;
              setNewYourName(_newYourName);
              setIsDisabledNewButton(
                !_newYourName || !newFriendName || !newFriendNote
              );
            }}
          />

          <FormControl
            required
            sx={{
              minWidth: 120
            }}
          >
            <InputLabel>ご関係</InputLabel>
            <Select
              value={newFriendNote}
              onChange={handleChangeNewClientNote}
              label='ご関係'
            >
              <MenuItem value='職場同僚'>職場同僚</MenuItem>
              <MenuItem value='友人'>友人</MenuItem>
              <MenuItem value='親族'>親族</MenuItem>
              <MenuItem value='取引先'>取引先</MenuItem>
              <MenuItem value='その他'>その他</MenuItem>
            </Select>
          </FormControl>

          <Button
            variant='contained'
            color='primary'
            startIcon={<AddIcon />}
            disabled={isDisabledNewButton}
            onClick={async () => {
              try {
                const res = await fetch(
                  `${process.env.API_URL}/api/partners/clients/friends`,
                  {
                    method: 'POST',
                    body: JSON.stringify({
                      partnersClientUuid,
                      friendName: newFriendName,
                      yourName: newYourName,
                      note: newFriendNote,
                      createdBy: 'user'
                    })
                  }
                );

                if (res.status >= 400) {
                  throw new Error('Bad response /api/partners/clients');
                }

                setIsOpenSnackbar(true);
                setSeverity('success');
                setMessage(`${newFriendName} 様の情報を追加しました`);

                setNewFriendName('');
                setNewYourName('');
                setNewFriendNote('');
                setIsDisabledNewButton(true);

                setReload(reload + 1);
              } catch (e) {
                alert('error');
                console.log(e);
              }
            }}
          >
            新規作成
          </Button>
        </Box>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>URL</TableCell>
                <TableCell>ご友人・ご知人のお名前</TableCell>
                <TableCell>お客様の氏名（ニックネーム可）</TableCell>
                <TableCell>ご関係</TableCell>
                <TableCell>操作</TableCell>
                <TableCell>タイムスタンプ</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {clientsFriends === undefined || !clientsFriends[0] ? (
                <TableRow>
                  <TableCell colSpan={6}>
                    <Typography align='center'>
                      {clientsFriends === undefined ? (
                        <>
                          Loading...
                          <LinearProgress />
                        </>
                      ) : (
                        'まだご紹介データがありません。『新規作成』より作成ください。'
                      )}
                    </Typography>
                  </TableCell>
                </TableRow>
              ) : (
                clientsFriends.map((clientsFriend, i) => (
                  <ClientsFriendsTableRow
                    key={i}
                    clientsFriend={clientsFriend}
                    reload={() => setReload(reload + 1)}
                  />
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <Stack spacing={2} sx={{ width: '100%' }}>
          <Snackbar
            open={isOpenSnackbar}
            autoHideDuration={5000}
            onClose={handleCloseSnackbar}
          >
            <Alert severity={severity}>{message}</Alert>
          </Snackbar>
        </Stack>
      </Container>
    </>
  );
};

export default InvitationsIndexPage;
